import { Component, OnInit, Input } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppDataService } from '../../models/app-data/app-data.service';
import { ApiDataService } from '../../models/api-data/api-data.service';
import { WfeStepLoaderService } from '../../services/wfe-step-loader.service';
import { Router } from '@angular/router';
import { SharedEventsServiceService } from '../../services/shared-events-service.service';
import { MatDialog as MatDialog } from '@angular/material/dialog';
import { MatSnackBar as MatSnackBar } from '@angular/material/snack-bar';
import { WFEEventListHandler } from '../../utils/wfe-event-list-handler-util';
import { CeeApiService } from '../../services/cee-api-service.service';
import { CEEInternalEmitterService } from '../../services/internal-cee-emitter-service.service';
import { BreadcrumbCellRendererComponent } from '../breadcrumb-cell-renderer/breadcrumb-cell-renderer.component';
import { CommonModule } from '@angular/common';

@Component({
    selector: 'app-breadcrumb-renderer',
    templateUrl: './breadcrumb-renderer.component.html',
    styleUrls: ['./breadcrumb-renderer.component.scss'],
    standalone: true,
    imports: [CommonModule , BreadcrumbCellRendererComponent]
})

export class BreadcrumbRendererComponent implements OnInit {
    autoPreviewConfigPath = localStorage.getItem('ENABLE_AUTO_PREVIEW')?.toLowerCase() == "true" && localStorage.getItem('AUTO_PREVIEW_CONFIG_PATH') ? localStorage.getItem('AUTO_PREVIEW_CONFIG_PATH') : 'assets/config';
    configBlockPath = this.autoPreviewConfigPath + '/application_blocks/application_blocks_';

    @Input() declare fieldData: any;
    @Input() declare stepId: any;

    childBlockIds: any;
    progressBarMappingData = {};
    eventListHandlerUtil: WFEEventListHandler;

    constructor(
        private http: HttpClient,
        private apiDataService: ApiDataService,
        private appDataService: AppDataService,
        private stepLoaderService: WfeStepLoaderService,
        private router: Router,
        private sharedEventService: SharedEventsServiceService,
        public internalCEEEmitter: CEEInternalEmitterService,
        private snackBar: MatSnackBar,
        private dialog: MatDialog,
        private ceeApiService: CeeApiService
    ) {
        this.eventListHandlerUtil = new WFEEventListHandler(
            ceeApiService,
            router,
            sharedEventService,
            internalCEEEmitter,
            stepLoaderService,
            appDataService,
            apiDataService,
            dialog,
            snackBar,
            http)
    }

    ngOnInit() {
        if (this.fieldData.possible_values) {
            for (const value of this.fieldData.possible_values) {
                this.progressBarMappingData[value.split('|')[0].trim()] = {
                    apiValue: value.split('|')[0].trim(),
                    label: value.split('|')[1].trim(),
                    color: value.split('|')[2].trim()
                };
            }
        }

        if (this.fieldData.additional_parameters && this.fieldData.additional_parameters.length > 0) {
            for (const parameter of this.fieldData.additional_parameters) {
                switch (parameter.parameter_type) {
                    case 'default_value':
                        this.childBlockIds = parameter.value.split('|');
                        break;
                }
            }
        }
    }

    triggerComponentEvent() {
        for (const event of this.fieldData.event_list) {
            if (event.isDefault && event.event_name.toLowerCase() === 'onclick') {
                this.eventListHandlerUtil.switchEventDisplayType(event, this.fieldData.event_list);
            }
        }
    }
}
